    $( '#sf_files' ).on( 'change', function () {
        var file;
        var reader = new FileReader();

        if ( !this.files ) {
            alert( 'Sorry your browser does not support files API' );
            return;
        }
        if ( !this.files[0] ) {
            alert( 'No file selected' );
            return;
        }

        file = this.files[0];

        reader.onload = (function ( file ) {
            return function (e) {
                var json;

                try {
                    json = JSON.parse( e.target.result) ;
                } catch ( er ) {
                    alert('ex when trying to parse json = ' + er);
                }

                console.log( json );

                if ( confirm('Are you sure you want to apply imported settings? This action cannot be reverted')) {
                    $.post( '/wp-admin/admin-post.php', {
                        action: 'sfm_import_settings',
                        data: json,
                        dataType: 'json'
                    }).done( function ( data ) {
                        console.log( data );
                        location.reload( );
                    }).fail( function () {
                        alert( "request error" );
                    })
                }
            }
        })( file );

        reader.readAsText( file );

    })


    function traversePages(pages) {
        var res = {};

        pages.each(function (i, el) {
            var t = $(el);
            var val = t.val();
            if (t.is(':checked')) res[val] = 1;
        });

        return res
    }

    function showLoadingView() {
        $('#fade-overlay').addClass('loading');
    }

    function hideLoadingView() {
        $('#fade-overlay').removeClass('loading');
    }


    function isScrolledIntoView($elem, elemTop, elemBottom, rule) {
        var docViewTop = $win.scrollTop();
        var docViewBottom = docViewTop + $win.height();

        return rule === 'after' ? docViewBottom > elemBottom + 50 : (elemBottom <= docViewBottom && elemTop >= docViewTop - 25) || (elemBottom > docViewBottom && elemTop < docViewTop - 25);
    }

    var state = 'in';
    var $tabs = $('#tabs-copy');
    var $subtabs = $('#sf_appearance_tabs');
    var $or = $('#tabs');
    var $form = $('#sf-options-wrap form')

    $win.on('scroll',function () {

        var elemTop = $tabs.offset().top;
        var elemBottom = elemTop + $tabs.height();
        if (isScrolledIntoView($tabs, elemTop, elemBottom, 'in')) {
            if (state !== 'in') {
                state = 'in';
                $or.removeClass('transition-in');
                //setTimeout(function () {
                $body.removeClass('fixed-nav');
                $or.css({'width': '', left: ''});
                //}, 50)
            }
        } else {
            if (state !== 'out') {
                state = 'out';
                $body.addClass('fixed-nav');
                $or.css({'width': $tabs.width(), left: $form.offset().left});
                $subtabs.css({'width': $tabs.width(), left: $form.offset().left});
                //setTimeout(function () {
                $or.addClass('transition-in');
                //}, 100)
            }
        }

    });

    $win.on('resize',function () {
        if ($body.is('.fixed-nav')) {
            $or.css({'width': $tabs.width(), left: $form.offset().left})
        }
    })

